@import "_variables.scss";
@import "_mixins.scss";

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 10px;
}

body {
  background-color: lighten($primary-color, 45%);
}

.container {
  max-width: 120rem;
  margin: 0 auto;
}

.heading {
  font-size: 3rem;
  font-family: $montserrat;
  text-align: center;
  font-weight: 500;
}

.underline {
  width: 5rem;
  height: 0.3rem;
  margin: 1rem auto;
  background-color: $primary-color;
}

a {
  display: inline-block;
}

a:hover {
  text-decoration: none;
}

:focus {
  outline: none;
}

textarea {
  resize: none;
}

.lang-active {
  background: #fff;
  @include border-radius;
}
